<style>
.panel {
  background-color: #00224b;
  border-radius: 8px 8px 0 0;
  margin: 0;
  padding: 0;
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  width: 100%;
}
.panel-list {
  display: flex;
  height: 52px;
  width: 100%;
  padding: 0 0 8px;
}
.panel-item {
  display: inline-flex;
  padding: 0;
  width: 20%;
}
.panel-list, .panel-item {
  align-items: flex-end;
  justify-content: center;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 0;
}
.panel-item-main {
  height: 60px;
  width: 60px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  background-color: #ffb81e;
  border-radius: 50%;
  box-shadow: transparent;
  margin: 0 auto 1px;
  transition: box-shadow .3s ease;
}
.panel-item-main:hover {
  box-shadow: 0 0 40px rgba(255,184,30,.3);
}
.panel-item-main .panel-name {
  display: none;
}
.panel-link {
  color: hsla(0, 0%, 100%, .8);
  display: flex;
  gap: 2px;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-end;
  font-size: 8px;
  font-weight: 800;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 100%;
}
</style>
