<style>
a {
  text-decoration: none;
  color: white;
}
.col-2 {
  width: 240px;
  z-index: 101;
}
.btn-scroll-top {
  display: inline-flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #ffb81e;
  border: none;
  border-radius: 50%;
  color: #000c1e;
  cursor: pointer;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  transform: scale(1);
  position: fixed;
  left: 16px;
  bottom: -110px;
  z-index: 100;
  opacity: 0;
  transition: transform .3s ease, bottom .6s ease-in-out, opacity .3s ease;
}
.btn-scroll-top.show {
  bottom: 70px;
  opacity: 1;
}
.btn-scroll-top:hover {
  transform: scale(1.2);
}
.live-chat-btn {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  background-color: #ffb81e;
  border: none;
  border-radius: 50%;
  color: #000c1e;
  cursor: pointer;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  transform: scale(1);
  position: fixed;
  right: 16px;
  bottom: 70px;
  z-index: 1026;
  transition: transform .3s ease;
}
