  transform: scale(1.2);
}
@media screen and (min-width: 768px){
  .btn-scroll-top {
    left: 104px;
  }
  .btn-scroll-top.show {
    bottom: 20px;
  }
  .live-chat-btn {
    right: 104px;
    bottom: 20px;
  }
}
@media screen and (min-width: 1248px) {
  .home {
    padding-left: 240px;
  }
  .btn-scroll-top {
    left: 272px;
  }
  .live-chat-btn {
    right: 32px;
