<script>
// @ is an alias to /src
import NavBar from '@/components/NavBar.vue'
import HeaderNavbar from '@/components/HeaderNavbar.vue'
import FooterPart from '@/components/FooterPart.vue'
import PanelModal from '@/components/PanelModal.vue'
export default {
  name: 'HomeView',
  data () {
    return {
      isDesktop: true,
      isPhone: false
    }
  },
  components: {
    NavBar,
    HeaderNavbar,
    FooterPart,
    PanelModal
  },
  methods: {
    checkWindowWidth () {
      this.isDesktop = window.innerWidth >= 1248
      this.isPhone = window.innerWidth <= 767
    },
    scrollTopBtn () {
      const scrollTopBtn = this.$refs.scrollTopBtn
      const onScroll = (event) => {
        const scrollPostion = event.target.scrollingElement.scrollTop
        scrollTopBtn.classList.toggle('show', scrollPostion > 0)
      }
      document.addEventListener('scroll', onScroll)
    },
    scrollTop () {
      window.scrollTo({
        top: 0, behavior: 'smooth'
      })
    }
  },
  mounted () {
    this.checkWindowWidth()
    window.addEventListener('resize', this.checkWindowWidth)
    this.scrollTopBtn()
  },
  beforeUnmount () {
    window.removeEventListener('resize', this.checkWindowWidth)
  }
}
</script>
